<template>
  <el-dialog
    :visible="true"
    :before-close="handleClose"
    width="90%"
    class="chain-info-dialog"
  >
    <div class="header" slot="title">
      <h4 class="title">区块链信息</h4>
    </div>
    <div class="chain-info-wrapper">
      <div class="row">
        <span class="label">时间戳：</span>
        <span class="value">{{ blockChain.blockTimestamp }}</span>
      </div>
      <div class="row">
        <span class="label">所在区块号：</span>
        <span class="value">{{ blockChain.blockNum }}</span>
      </div>
      <div class="row">
        <span class="label">交易id：</span>
        <span class="value">{{ blockChain.transHash }}</span>
      </div>
      <div class="row">
        <span class="label">区块哈希：</span>
        <span class="value">{{ blockChain.blockHash }}</span>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import { getBlockChain } from 'models/Api';

export default {
  name: 'ChainInfo',
  props: {
    txnId: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      showDialog: true,
      loading: false,
      // {
      //   blockHash: string, // 区块哈希
      //   blockNum: number, // 所在区块号
      //   blockTimestamp: string, // 时间戳
      //   transHash: string, // 交易id
      // }
      blockChain: {} // 区块链信息
    }
  },
  methods: {
    /**
     * 获取区块链信息
     */
    async getBlockChain () {
      const txnId = this.txnId;
      this.loading = true;

      const {
        code,
        data,
        message
      } = await getBlockChain(txnId).catch((err) => {
        console.log(err);
        this.loading = false;
      });

      if (code !== 0 || !data || JSON.stringify(data) === '{}') {
        console.log(message);
      } else {
        this.blockChain = data;
      }

      this.loading = false;
    },

    handleClose () {
      this.$emit('close');
    }
  },
  created () {
    this.getBlockChain();
  }
}
</script>

