<template>
  <section class="license-info-wrapper">
    <header class="header">
      <h2 class="title">{{ permit.permitType }}</h2>

      <div class="actions">
        <!-- <button
          class="btn-chain-info"
          @click="handleBtnClick('chainInfo')"
        >
          <span class="icon"></span>
          <span class="text">区块链信息</span>
        </button> -->
        <button
          v-stat="statInfo.download"
          class="btn-download"
          :class="downloadDisabled ? 'disabled' : ''"
          :disabled="downloadDisabled"
          @click="handleBtnClick('download')"
        >下载</button>
      </div>
    </header>

    <ul class="info">
      <li class="item">
        <span class="label">证照编号：</span>
        <span class="value">{{ permit.permitNumber }}</span>
      </li>
      <li class="item">
        <span class="label">企业名称：</span>
        <span class="value">{{ permit.oprName }}</span>
      </li>
      <li class="item">
        <span class="label">统一社会信用代码：</span>
        <span class="value">{{ permit.uniformSocialCreditCode }}</span>
      </li>
      <li class="item">
        <span class="label">颁发机构：</span>
        <span class="value">{{ permit.permitIssuingAgency }}</span>
      </li>
      <li class="item">
        <span class="label">颁发日期：</span>
        <span class="value">{{ permit.permitIssuedTime | formatDate }}</span>
      </li>
    </ul>
  </section>
</template>

<script>
import { STAT_ACTION } from 'config/stat.config.js';

export default {
  name: 'LicenseInfo',
  props: {
    permit: {
      type: Object,
      default () {
        return {};
      }
    },
    downloadDisabled: {
      type: Boolean,
      default () {
        return true;
      }
    }
  },
  data () {
    return {
      statInfo: {
        download: {
          action: STAT_ACTION.DOWNLOAD
        }
      }
    }
  },
  filters: {
    /**
     * @param { string } data - 格式为 YYYY-MM-DD hh:mm:ss 的日期字符串
     * @return { string } 返回格式为 YYYY-MM-DD 的日期字符串
     */
    formatDate (date = '') {
      const [newDate] = date.split(' ') || [];

      return newDate;
    }
  },
  methods: {
    handleBtnClick (type) {
      switch (type) {
        // // 区块链信息按钮点击事件
        // case 'chainInfo':
        //   this.$emit('showChainInfo');
        //   break;

        // 下载按钮点击
        case 'download':
          this.$emit('download')
          break;

        default:
          break;
      }
    }
  }
}
</script>

