/**
 * 证照图片生成插件
 * @requires qrcode - 二维码生成插件 https://github.com/soldair/node-qrcode
 */

// measureText 检查字体宽度

import QRCode from 'qrcode';

class PermitPicture {

  constructor (options = {}) {
    // 默认配置
    let _options = {
      // 全局字体设置


      fontSize: 18, // 18pt
      fontColor: '#231F20',
      fontFamily: 'Simsun',
      fontMargin: 20, // 18pt
      // font: {
      //   family: '宋体',
      //   size: '18pt',
      //   color: '#231F20',
      //   margin: 18, // 字体间隔
      // },

      pageSize: [1123, 794], // 96dpi
      // largePageSize: [3508, 2481], // 300dpi

      // 需要特殊处理的字段
      specialFields: []
    }

    this.options = Object.assign(_options, options);

    this.specialFields = this.options.specialFields;

    this.boards = []; // 画板容器
  }

  /**
   * 重设全局配置信息
   * @param { object } options - 配置信息
   */
  setOptions (options = {}) {
    this.options = Object.assign({}, this.options, options);
    this.specialFields = this.options.specialFields;
  }

  /**
   * 获取canvas
   */
  get canvas () {
    return this.boards.map((item) => item.canvas);
  }

  /**
   * 获取图片
   * @return { array } - 返回一个所有由该实例生成的证照图url地址组成的数据
   */
  get pictures () {
    let [width, height] = this.options.pageSize,
        canvas = this.canvas;

    let oCanvas,
        ctx;


    return canvas.map((item) => {
      oCanvas = document.createElement('canvas');
      ctx = oCanvas.getContext('2d');

      oCanvas.width = width;
      oCanvas.height = height;

      ctx.drawImage(item, 0, 0, width, height);
      return oCanvas.toDataURL('image/jpeg', 1.0);
    });
  }

  /**
   * 获取大图
   */
  get largePictures () {
    return this.canvas.map((item) => {
      return item.toDataURL('image/jpeg', 1.0);
    })
  }

  /**
   * 提供接口获取不同尺寸的图片
   * @param { number } width - 宽度
   * @param { number } quality - 图片质量
   * @return { string[] }
   */
  async getPictures (width, quality = 1.0) {
    let height,
        _w = width,
        canvas = this.canvas;
        // canvas = this.canvas,
        // len = canvas.length;

    let oCanvas,
        ctx;

    switch (width) {
      case 720:
        height = 509;
        break;
      case 1080:
        height = 764;
        break;
      case 1754:
        height = 1240;
        break;
      case 3508:
        height = 2481;
        break;
      case 4960:
        height = 3508;
        break;
      default:
        _w = 1080;
        height = 764;
        break;
    }

    // const pictures = [];
    // for (let i = 0; i < len; i ++) {
    //   oCanvas = document.createElement('canvas');
    //   ctx = oCanvas.getContext('2d');

    //   oCanvas.width = _w;
    //   oCanvas.height = height;

    //   await ctx.drawImage(canvas[i], 0, 0, _w, height);
    //   pictures[i] = oCanvas.toDataURL('image/jpeg', quality);
    // }

    // return pictures;

    return canvas.map((item) => {
      oCanvas = document.createElement('canvas');
      ctx = oCanvas.getContext('2d');

      oCanvas.width = _w;
      oCanvas.height = height;

      ctx.drawImage(item, 0, 0, _w, height);
      return oCanvas.toDataURL('image/jpeg', quality);
    });
  }

  /**
   * 获取不同分辨率下的尺寸值
   * @param { number } size - pt单位的尺寸值
   * @return { number } 返回一个px单位的尺寸值
   */
  getSize (size) {
    // 默认为96dpi的 1123
    let _default = 1123,
        pageSize = this.options.pageSize;
        // largePageSize = this.options.largePageSize;

    return Math.round(size * (pageSize[0] / _default));

    // return {
    //   normal: Math.round(size * (pageSize[0] / _default)),
    //   large: Math.round(size * (largePageSize[0] / _default))
    // }
    
  }

  /**
   * 计算样式
   * @param { CanvasRenderingContext2D } ctx - Canvas 2d 上下文
   * @param { string } text - 文本
   * @param { string } fieldName - 字段名称
   * @param { number } maxWidth - 限制的宽度
   */
  _computed (ctx, text, fieldName, maxWidth) {
    let specialFields = this.specialFields,
        item = specialFields.find((item) => item.name === fieldName);

    if (!item) {
      item = {
        maxRow: 2
      }
    }
    let fontFamily = this.options.fontFamily,
        fontSize = this.options.fontSize;


    let size = this.getSize(fontSize);
    ctx.font = `normal ${size}px ${fontFamily}`;

    let textWidth = ctx.measureText(text).width,
        maxRow = item.maxRow;

    while (textWidth / maxWidth > maxRow) {
      fontSize -= 2;
      size = this.getSize(fontSize);
      ctx.font = `normal ${size}px ${fontFamily}`;  
      textWidth = ctx.measureText(text).width
      // console.log('fontSize', fontSize, '--textWidth', textWidth, maxWidth)
    }

    // console.log('最终字体大小: ',fontSize, size)

    return {
      size
    }

    // console.log(item, textWidth, maxWidth, textWidth / maxWidth)
  }

  /**
   * 创建canvas
   * @return { Promise<object> }
   */
  _createCanvas () {
    return new Promise((resolve, reject) => {
      const {
        pageSize: [width, height]
      } = this.options;

      const oCanvas = document.createElement('canvas'),
            ctx = oCanvas.getContext('2d');

      oCanvas.width = width;
      oCanvas.height = height;

      // console.log(width, height)

      ctx.fillStyle = '#fff';
      ctx.fillRect(0, 0, width, height); // 填充白色背景

      this.boards.push({
        canvas: oCanvas,
        ctx
      });

      // document.body.appendChild(oCanvas)
      
      resolve({
        canvas: oCanvas,
        ctx
      })
    });
  }

  /**
   * 绘制底图
   * @param { CanvasRenderingContext2D } ctx - canvas 2d 上下文
   * @param { string } url - 需要绘制图片的url
   * @param { object } options - 图片参数
   * @param { number } options.width - 图片宽度
   * @param { number } options.height - 图片高度
   * @param { number } options.x
   * @param { number } options.y
   * @return { Promise<void> }
   */
  _drawBgImg (ctx, url, options) {
    return new Promise((resolve, reject) => {
      const img = new Image();

      img.onload = () => {
        const { x, y, width, height } = options;
        ctx.drawImage(img, x, y, width, height);

        resolve();
      }

      img.onerror = function (err) {
        reject(err);
      }

      img.src = url;
    })
  }

  /**
   * 绘制二维码
   * @requires qrcode
   * @param { CanvasRenderingContext2D } ctx - canvas 2d 上下文
   * @param { object } options - 生成二维码的配置信息
   * 
   * @return { Promise<any> }
   */
  async _drawQRCode (ctx, options) {
    if (!QRCode) {
      throw new ReferenceError('Cannot find module "QRCode".');
    }

    const {
      url,
      x = 0,
      y = 0,
      width = 200,
      margin = 1,
      scale = 4
    } = options;

    const size = this.getSize(width); // 需要计算适应尺寸

    let qrcode = await QRCode.toCanvas(url, {
      margin,
      width: size,
      scale
    });

    if (!qrcode) {
      reject('二维码生成失败，请检查配置参数是否有误。');
    }

    const {
      pageSize
    } = this.options;

    await ctx.drawImage(qrcode, x * pageSize[0], y * pageSize[1]);
  }

  /**
   * 绘制水印
   * @param { CanvasRenderingContext2D } ctx - canvas 2d 上下文
   * @param { object } options - 水印的配置信息
   */
  async _drawWatermark (ctx, options) {
    return new Promise((resolve, reject) => {
      const img = new Image(),
            { pageSize } = this.options;

      img.onload = () => {
        // const { x, y, width, height } = options;
        const { width, height } = options,
              x = pageSize[0] - this.getSize(width + 50),
              y = this.getSize(50);

        ctx.drawImage(img, x, y, this.getSize(width), this.getSize(height));

        resolve();
      }

      img.onerror = function (err) {
        reject(err);
      }

      img.src = options.url;
    })    
  } 

  /**
   * 文本填充
   * @param { CanvasRenderingContext2D } ctx - canvas 2d 上下文
   * @param { array } fields - 需要填充的文本数据
   * @param { string } width - canvas 宽度
   * @param { string } height - canvas 高度
   * 
   * @return { Promise<void> }
   */
  async _fillText (ctx, fields, width, height) {
    let len = fields.length,
        value,
        coordinate,
        maxWidth,
        x,
        y;

    for (let i = 0; i < len; i ++) {
      ({ value, coordinate, maxWidth = 1, name } = fields[i]);

      if (!value) {
        return false;
      }
  
      if (typeof value === 'string') {
        ([x, y] = coordinate);
        // ctx.fillText(value, x * width, y * height);
        await this._fillTextLimitWidth(ctx, value, name, x * width, y * height, maxWidth * width);
      } else {
        value.forEach((v, i) => {
          ([x, y] = coordinate[i]);
          ctx.fillText(v, x * width, y * height);
          // this._fillTextLimitWidth(ctx, v, x * width, y * height, maxWidth * width);
        })
      }
    }
  }

  /**
   * 绘制文本，并限制宽度，自动换行
   * @param { CanvasRenderingContext2D } ctx
   * @param { string } text - 需要绘制的文本
   * @param { string } fieldName - 当前绘制的字段名称
   * @param { number } x - x坐标
   * @param { number } y - y坐标
   * @param { number } maxWidth - 限制的宽度
   * 
   * @return { Promise<void> }
   */
  async _fillTextLimitWidth (ctx, text, fieldName, x, y, maxWidth = 1) {
    let row = [],
        chr = text.split(''),
        len = chr.length,
        temp = '',
        item;

    let {
      fontFamily: family,
      fontSize: size,
      fontColor: color,
      fontMargin: margin
    } = this.options;

    // 计算字体大小
    size = this.getSize(size)
    // 计算字体间隔
    margin = this.getSize(margin)

    ctx.font = `normal normal ${size}px ${family}`;
    ctx.fillStyle = color;

    size = this._computed(ctx, text, fieldName, maxWidth); //

    // console.log(text, maxWidth, ctx.measureText(text).width);

    for (let i = 0; i < len; i ++) {
      item = chr[i];
      if (ctx.measureText(temp).width < maxWidth && ctx.measureText(temp + item).width <= maxWidth) {
        temp += item;
      } else {
        row.push(temp);
        temp = item; // 重开一行
      }
    }
    row.push(temp);

    let rowLen = row.length;
    
    if (rowLen > 1) {
      for (let j = 0; j < rowLen; j ++) {
        size -= 2;
      }
    }

    // console.log(row, size)

    ctx.font = `normal normal ${size}px ${family}`;

    for (let j = 0; j < rowLen; j ++) {
      await ctx.fillText(row[j], x, y + j * margin);
    }
  }

  /**
   * 绘制图片
   * @param { object } pictureInfo - 需要绘制的图片信息
   * @param { string } pictureInfo.bgImgUrl - 底图url地址
   * @param { object } [pictureInfo.qrcode] - 二维码图片配置 - https://github.com/soldair/node-qrcode
   * @param { string } pictureInfo.qrcode.url - 需要生成的二维码的url地址
   * @param { { coordinate: string | [], value: string | [] }[] } [pictureInfo.fields] - 需要绘制文本信息
   * 
   * @return { Promise<void> }
   */
  async drawPicture (pictureInfo) {
    const {
      bgImgUrl,
      qrcode = null,
      watermark = null,
      fields = []
    } = pictureInfo;

    const [width, height] = this.options.pageSize;

    const {
      canvas,
      ctx
    } = await this._createCanvas();
    
    // 绘制底图
    await this._drawBgImg(ctx, bgImgUrl, {
      x: 0,
      y: 0,
      width,
      height
    });

    // 填充文本数据
    if (fields && fields.length > 0) {
      await this._fillText(ctx, fields, width, height);
    }

    // 绘制二维码
    if (qrcode && qrcode.url) {
      await this._drawQRCode(ctx, qrcode);
    }

    // 绘制水印
    if (watermark && watermark.url) {
      await this._drawWatermark(ctx, watermark);
    }
  }

}

export default PermitPicture;